import Vue from "vue";
import VuiIcon from "vui-design/components/icon";
import VuiModal from "./src/modal";
import createChainedFunction from "vui-design/utils/createChainedFunction";
import is from "vui-design/utils/is";
import getClassNamePrefix from "vui-design/utils/getClassNamePrefix";

/**
* 
*/
const defaults = {
  width: 360,
  getPopupContainer: () => document.body
};

/**
*  Modal 
* @param {Object} options 
*/
const createModalInstance = function(options) {
  //  Modal  html 
  const container = options.getPopupContainer();
  const el = document.createElement("div");

  container.appendChild(el);

  //  Modal  html  getPopupContainer 
  options.getPopupContainer = false;

  //  Modal 
  return new Vue({
    el,
    components: {
      VuiModal
    },
    data() {
      return {
        ...options,
        visible: false
      };
    },
    methods: {
      open() {
        this.visible = true;
      },
      close() {
        this.visible = false;
      },
      update(options) {
        if (!is.plainObject(options)) {
          return;
        }

        for(let key in options) {
          this[key] = options[key];
        }
      },
      handleBeforeOpen() {

      },
      handleOpen() {

      },
      handleAfterOpen() {

      },
      handleBeforeClose() {

      },
      handleClose() {

      },
      handleAfterClose() {
        this.$destroy();
        this.$el.parentNode && this.$el.parentNode.removeChild(this.$el);
      }
    },
    render(h) {
      const { type, icon, visible, showCancelButton, cancelButtonProps, cancelText, cancelAsync, showOkButton, okButtonProps, okText, okAsync, top, centered, width, className, headerStyle, bodyStyle, footerStyle, backdrop, backdropClassName, animations, getPopupContainer } = this;
      const { onCancel, onOk, onBeforeOpen, onOpen, onAfterOpen, onBeforeClose, onClose, onAfterClose, handleOpen, handleBeforeOpen, handleAfterOpen, handleBeforeClose, handleClose, handleAfterClose } = this;

      const beforeOpen = createChainedFunction(handleBeforeOpen.bind(this), onBeforeOpen);
      const open = createChainedFunction(handleOpen.bind(this), onOpen);
      const afterOpen = createChainedFunction(handleAfterOpen.bind(this), onAfterOpen);
      const beforeClose = createChainedFunction(handleBeforeClose.bind(this), onBeforeClose);
      const close = createChainedFunction(handleClose.bind(this), onClose);
      const afterClose = createChainedFunction(handleAfterClose.bind(this), onAfterClose);

      // attrs
      let attrs = {
        props: {
          visible,
          showNotice: true,
          showCancelButton,
          cancelButtonProps,
          cancelText,
          cancelAsync,
          showOkButton,
          okButtonProps,
          okText,
          okAsync,
          closable: false,
          top,
          centered,
          width,
          className,
          headerStyle,
          bodyStyle,
          footerStyle,
          backdrop,
          backdropClassName,
          clickBackdropToClose: false,
          animations,
          getPopupContainer
        },
        on: {
          beforeOpen,
          open,
          afterOpen,
          beforeClose,
          close,
          afterClose
        }
      };

      if (is.function(onCancel)) {
        attrs.on.cancel = onCancel;
      }

      if (is.function(onOk)) {
        attrs.on.ok = onOk;
      }

      // title
      const title = is.function(this.title) ? this.title(h) : this.title;

      // description
      const description = is.function(this.description) ? this.description(h) : this.description;

      // class
      const classNamePrefix = getClassNamePrefix(this.classNamePrefix, "modal-notice");
      let classes = {};

      classes.elNotice = {
        [`${classNamePrefix}`]: true,
        [`${classNamePrefix}-${type}`]: type
      };
      classes.elNoticeTitle = `${classNamePrefix}-title`;
      classes.elNoticeDescription = `${classNamePrefix}-description`;
      classes.elNoticeIcon = `${classNamePrefix}-icon`;

      // render
      return (
        <VuiModal {...attrs}>
          <div class={classes.elNotice}>
            <div class={classes.elNoticeIcon}>
              <VuiIcon type={icon} />
            </div>
            <div class={classes.elNoticeTitle}>{title}</div>
            {
              description && (
                <div class={classes.elNoticeDescription}>{description}</div>
              )
            }
          </div>
        </VuiModal>
      );
    }
  });
};

/**
*  info 
* @param {Object} options 
*/
VuiModal.info = function(options) {
  if (is.server) {
    return;
  }

  if (!is.plainObject(options)) {
    return;
  }

  options = {
    type: "info",
    icon: "info",
    ...defaults,
    ...options
  };

  options.showCancelButton = false;
  options.showOkButton = true;

  const instance = createModalInstance(options);

  instance.open();

  return instance;
};

/**
*  warning 
* @param {Object} options 
*/
VuiModal.warning = function(options) {
  if (is.server) {
    return;
  }

  if (!is.plainObject(options)) {
    return;
  }

  options = {
    type: "warning",
    icon: "warning",
    ...defaults,
    ...options
  };

  options.showCancelButton = false;
  options.showOkButton = true;

  const instance = createModalInstance(options);

  instance.open();

  return instance;
};

